@charset "UTF-8";
/* ----- App.js  ----- */
body {
  background: url("../img/bg.jpg") center center fixed no-repeat;
  background-size: cover;
  padding: 0px;
  margin: 0px; }

* {
  padding: 0px;
  margin: 0px; }

/*-- 因應 <Link /> 使用 --*/
a {
  text-decoration: none;
  color: inherit; }

/* ----- Nav.js  ----- */
.window-UnMoving {
  background-color: transparent;
  position: fixed;
  top: 0;
  height: 46px;
  transition: 0.5s all;
  width: 100%;
  z-index: 1;
  display: flex;
  align-items: center; }
  .window-UnMoving ul li.active {
    transition: 0.5s all;
    background-color: #eba500;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }

.window-is-Moving {
  background-color: #eba500;
  position: fixed;
  top: 0;
  height: 46px;
  width: 100%;
  transition: 0.5s all;
  z-index: 1;
  box-shadow: 0px 4px 3px rgba(51, 51, 102, 0.5);
  display: flex;
  align-items: center; }
  .window-is-Moving ul li.active {
    transition: 0.5s all;
    color: #0074bd; }

ul {
  list-style: none; }
  ul li {
    font-size: 18px;
    color: white;
    font-weight: bold;
    font-family: monospace;
    text-align: center;
    outline: none;
    display: inline-block;
    padding: 12px; }
    ul li ~ li {
      cursor: pointer;
      margin-left: 8px; }

/* ----- Home.js  ----- */
.page-title {
  font-size: 80px;
  color: white;
  font-family: Impact, Charcoal, sans-serif;
  position: relative;
  height: auto;
  width: 800px;
  border-bottom: 3px solid wheat;
  border-right: 3px solid wheat;
  margin: 20px;
  padding: 10px;
  box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }

/* ----- PostList.js  ----- */
.loading {
  font-size: 20px;
  color: white;
  font-family: monospace;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 150px;
  width: 150px;
  margin: 20px auto;
  padding: 70px; }

.wrapper {
  margin: 0 auto;
  min-height: 900px;
  height: auto;
  width: auto;
  padding: 30px; }
  .wrapper .board {
    height: auto;
    width: 1000px;
    background-color: rgba(110, 105, 105, 0.35);
    margin: 100px auto;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
    .wrapper .board .post {
      cursor: pointer;
      position: relative;
      height: 200px;
      width: 200px;
      border: 3px solid wheat;
      transition: 0.25s all;
      margin: 20px;
      padding: 30px;
      box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
      .wrapper .board .post:hover {
        border-color: #eba500;
        background-color: rgba(235, 165, 0, 0.2); }
      .wrapper .board .post-title {
        font-size: 23px;
        color: white;
        font-weight: bold;
        font-family: monospace;
        padding: 5px 8px;
        height: 50px;
        word-break: break-word;
        text-overflow: ellipsis;
        overflow: hidden;
        transition: 0.3s all;
        white-space: nowrap; }
      .wrapper .board .post-text {
        font-size: 13px;
        color: white;
        font-weight: bold;
        font-family: monospace;
        padding: 5px 8px;
        height: 91px;
        word-break: break-word;
        text-overflow: ellipsis;
        overflow: hidden; }
        .wrapper .board .post-text:hover {
          color: white; }
      .wrapper .board .post-editor {
        font-size: 13px;
        color: white;
        font-weight: bold;
        font-family: monospace;
        position: absolute;
        bottom: 25px;
        right: 25px;
        padding: 5px 8px;
        width: 110px;
        word-break: break-word;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden; }

/* ----- Post.js / About.js ----- */
.single-post {
  background-color: rgba(197, 188, 168, 0.1);
  position: relative;
  height: auto;
  width: 950px;
  border: 3px solid wheat;
  transition: 0.3s all;
  margin: 20px;
  padding: 60px;
  padding-bottom: 140px;
  box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5); }
  .single-post-title {
    font-size: 40px;
    color: wheat;
    font-weight: bold;
    font-family: monospace;
    padding: 5px 8px;
    height: auto;
    word-break: break-word;
    transition: 0.3s all; }
  .single-post-text {
    font-size: 16px;
    color: white;
    font-family: monospace;
    margin-top: 50px;
    padding: 5px 8px;
    height: auto;
    word-break: break-word;
    text-overflow: ellipsis;
    overflow: hidden;
    line-height: 2em; }
  .single-post-editor {
    font-size: 20px;
    color: white;
    font-weight: bold;
    font-family: monospace;
    position: absolute;
    bottom: 45px;
    right: 40px;
    padding: 5px 8px;
    word-break: break-word; }

/* ----- Write.js  ----- */
.write-article {
  font-size: 20px;
  color: white;
  font-weight: bold;
  font-family: monospace;
  position: relative;
  height: auto;
  width: 950px;
  border: 3px solid wheat;
  transition: 0.3s all;
  margin: 20px;
  padding: 60px;
  padding-bottom: 100px;
  box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5);
  display: flex;
  justify-content: center;
  flex-direction: column; }
  .write-article-title {
    font-size: 16px;
    color: white;
    font-weight: bold;
    font-family: monospace;
    transition: 0.7s all;
    outline: none;
    padding: 5px 20px;
    margin-top: 8px;
    margin-bottom: 60px;
    background-color: transparent;
    border: 0px;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5);
    border: 3px solid wheat; }
    .write-article-title:focus {
      background-color: rgba(235, 165, 0, 0.3); }
  .write-article-editor {
    font-size: 16px;
    color: white;
    font-weight: bold;
    font-family: monospace;
    transition: 0.7s all;
    outline: none;
    margin-top: 8px;
    margin-bottom: 60px;
    padding: 5px 20px;
    background-color: transparent;
    border: 0px;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5);
    border: 3px solid wheat; }
    .write-article-editor:focus {
      background-color: rgba(235, 165, 0, 0.3); }
  .write-article-text {
    font-size: 16px;
    color: white;
    font-family: monospace;
    transition: 0.7s all;
    outline: none;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5);
    resize: none;
    margin-top: 16px;
    margin-bottom: 30px;
    padding: 20px;
    height: 500px;
    word-break: break-word;
    background-color: transparent;
    border: 3px solid wheat; }
    .write-article-text:focus {
      background-color: rgba(235, 165, 0, 0.3); }
  .write-article-button {
    font-size: 25px;
    color: white;
    font-weight: bold;
    font-family: monospace;
    cursor: pointer;
    margin-top: 60px;
    transition: 0.4s all;
    outline: none;
    box-shadow: 5px 4px 3px rgba(51, 51, 102, 0.5);
    padding: 30px;
    border: 3px solid wheat;
    background-color: transparent; }
    .write-article-button:hover {
      color: #0074bd;
      background-color: #eba500; }
